<template>
  <div class="page-login">
    <div class="login-header">
      <router-link class="logo" to="/index"></router-link>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt=""
        />
      </div>
      <div class="form">
        <h4 v-if="error" class="tips">{{ error }}</h4>
        <p>
          <span></span>
        </p>
        <el-input
          :class="{error:error && !userName}"
          v-model="userName"
          placeholder="//"
          prefix-icon="profile"
        ></el-input>
        <el-input
          :class="{error:error && !passWord}"
          v-model="passWord"
          type="password"
          placeholder=""
          prefix-icon="password"
        ></el-input>
        <div class="foot">
          <a href="#"></a>
        </div>
        <el-button type="primary" class="btn-login" @click="submit"></el-button>
        <p class="reg">
           <span></span>
           <router-link :to="{name: 'register'}"></router-link>
        </p>
        <div class="oauth-wrapper J-oauth-wrapper">
            <h3 class="title-wrapper"><span class="title"></span></h3>
            <div class="oauth cf">
                <span class="oauth__link oauth__link--qq third-login-btn" data-href="/account/connect/tencent" target="_blank" id="J-third-tencent"></span>
                <span class="oauth__link oauth__link--weibo third-login-btn" data-href="/account/connect/sina" target="_blank" id="J-third-sina"></span>
            </div>
            <i id="thirdLoginRiskpartner" data-riskpartner="0"></i>
        </div>
      </div>
    </div>
    <footer>
      <ul>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
      </ul>
      <p>2021  meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      userName: '',
      passWord: '',
      error: ''
    }
  },
  methods: {
    submit () {
      if (!this.userName && !this.passWord) {
        this.error = ''
        return false
      }
      if (!this.userName) {
        this.error = ''
        return false
      }
      if (!this.passWord) {
        this.error = ''
        return false
      }
      api.login({
        params: {
          userName: this.userName,
          password: this.passWord
        }
      }).then((res) => {
        // console.log(res);
        if (res.data.status === 'success') {
          this.$router.push({name: 'index'})
          this.$store.commit('setUserName', this.userName)
        } else if (res.data.status === 'fail') {
          this.error = res.data.msg
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>



